.load-dot{
    display: inline-block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin: 0 5px;
    position: relative;
}

.load-dot::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: inherit;
    border-radius: 50%;
    z-index: -1;
    -webkit-animation: ripple 1.5s ease-out infinite;
            animation: ripple 1.5s ease-out infinite;
}

.load-dot:first-child{
    background-color: #f1b9af;
}

.load-dot:first-child::before{
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.load-dot:nth-child(2){
    background-color: #ff9e9a;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.load-dot:nth-child(2)::before{
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.load-dot:nth-child(3){
    background-color: #f16957;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
}

.load-dot:nth-child(3)::before{
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

.load-dot:nth-child(4){
    background-color: #fa5d49;
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}

.load-dot:nth-child(3)::before{
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.load-dot:last-child{
    background-color:#f84d40;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
}

.load-dot:last-child::before{
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes ripple{
    from{
        opacity: 1;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        opacity: 0;
        -webkit-transform: scale(2.3);
                transform: scale(2.3);
    }
}

@keyframes ripple{
    from{
        opacity: 1;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    to{
        opacity: 0;
        -webkit-transform: scale(2.3);
                transform: scale(2.3);
    }
}
