}

.detail-tab:hover, .tab:checked + .detail-tab{
    background: rgb(173, 173, 173);
    color: white;
}

.tab-body{
    clear: both;
    -webkit-perspective: 600px;
            perspective: 600px;
    margin-bottom: 500px;   
}

.tab-body .tab-detail{
    position: absolute;
    width: 600px;
    opacity: 0;
    border: 2px solid rgb(173, 173, 173);
    padding: 20px 25px 25px 30px;
    line-height: 1.5em;
    transition: opacity 0.3s, -webkit-transform 1s;
    transition: opacity 0.3s, transform 1s;
    transition: opacity 0.3s, transform 1s, -webkit-transform 1s;
    border-radius: 0 3px 3px;
    -webkit-transform: rotateX(-15deg);
            transform: rotateX(-15deg);
    -webkit-transform-origin: top center;
            transform-origin: top center;    
}

#tab1:checked ~ .tab-body div:nth-of-type(1),
#tab2:checked ~ .tab-body div:nth-of-type(2){
